.option,
.option-add {
    min-height: 14vh;
    padding-right: 10px;
    padding-left: 10px;
    /* change the height and width of option in media tag */
    min-width: 13vw;
    justify-content: center;
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px #f9ab00, 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}

.option:hover,
.user-ans,
.option:focus {
    height: auto;
    width: auto;
    cursor: pointer;
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px #1da7e7, 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
}

.question {
    min-height: 13vh;
    min-width: 38vw;
}

.question,
.choose-ans {
    box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px #243c47, 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072);
}

.question h4 {
    font-family: Dosis;
}

@media only screen and (max-width: 700px) {
    .option,
    .option-add {
        min-height: 14vh;
        padding-right: 10px;
        padding-left: 10px;
        /* change the height and width of option in media tag */
        min-width: 30vw;
        justify-content: center;
        box-shadow: 0 2.8px 2.2px rgba(0, 0, 0, 0.034), 0 6.7px 5.3px #f9ab00, 0 12.5px 10px rgba(0, 0, 0, 0.06), 0 22.3px 17.9px rgba(0, 0, 0, 0.072), 0 41.8px 33.4px rgba(0, 0, 0, 0.086), 0 100px 80px rgba(0, 0, 0, 0.12);
    }
}